import React from 'react'
import './Dialogs.css'
import DialogsUser from './DialogsUser/DialogsUser.js'
import DialogsMessage from './DialogsMessage/DialogsMessage.js'
import DialogsFormMessage from "./DialogsForm/DialogsMessageForm";


const Dialogs = (props) => {

	let DialogsUserData = props.messagePage.UserData.map(post => <DialogsUser name={post.name} key={post.id} id={post.id} img={post.img}/>);
	let DialogsMessageData = props.messagePage.MessageData.map(messages => <DialogsMessage key={messages.id} message={messages.message}/>);

	const onSubmitClick = (DialogsFormMessageData) => {
		props.addMesage(DialogsFormMessageData.dialogsMessageText)
	};

	return (
		<section className="Messages">
			<h2>Messages</h2>
			<div className="dialogs">
				<div className="dialogs__users">
					{DialogsUserData}
				</div>
				<div className="dialogs__messages">
					<div className="dialogs__messages-pos">
						{DialogsMessageData}
						<DialogsFormMessage onSubmit={onSubmitClick}/>
					</div>
				</div>
			</div>
		</section>
	)
}
export default Dialogs;