import React from 'react';
import handleClick from '../Dialog-click.js'
import {NavLink} from "react-router-dom";
import './DialogsUser.css'
const DialogsUser = (props) => {
	return(
		<div className={"dialogs__user number" + props.id} onClick={() => handleClick(props.id)}>
			<img src={props.img} alt=""/>
			<p><NavLink to={"/dialogs/" + props.id}>{props.name}</NavLink></p>
		</div>
	)
}
export default DialogsUser;
