import {Field, reduxForm} from "redux-form";
import React from "react";
import {maxlength, requiredField} from "../../../validation/validationForm";
import {TextareaContainer} from "../../Common/TextareaContainer/TextareaContainer";

let ez = maxlength(10);

const DialogsMessageForm = (props) => {

    return (
        <form onSubmit={props.handleSubmit} >
            <Field component={TextareaContainer} placeholder={'напишите что - нибудь'} name='dialogsMessageText' validate={[requiredField, ez]}/>
            <button>SEND</button>
        </form>
    )
};


export default reduxForm({form: 'DialogsMessageForm'})(DialogsMessageForm);